import { dateNormalize } from './date-normalize.js';

/**
 * @description
 * Date addition to gives a new value
 *
 * @example
 * ``` ts
 * dateFormat(dateNew('2017-9-25', 'd', 5), 'YYYY-MM-DD');
 * // => 2017-09-30
 * ```
 *
 * @param startDate Original time object.
 * @param newMode A date string expression that represents the time interval to add.
 * @param newNum Date numeric expression that represents the number of time intervals to add or substract, can be positive and negative
 * @return New time object.
 *
 * @docsCategory preference/utils/date
 * @codeFilter date-new
 */
export const dateNew = (
  startDate: Date | string | number,
  newMode: 'y' | 'q' | 'm' | 'w' | 'd' | 'h' | 'mi' | 's',
  newNum: number
) => {
  const newDate = dateNormalize(startDate);
  switch (newMode) {
    case 'y':
      newDate.setFullYear(newDate.getFullYear() + newNum);
      return newDate;
    case 'q':
      newDate.setMonth(newDate.getMonth() + newNum * 3);
      return newDate;
    case 'm':
      newDate.setMonth(newDate.getMonth() + newNum);
      return newDate;
    case 'w':
      newDate.setDate(newDate.getDate() + newNum * 7);
      return newDate;
    case 'd':
      newDate.setDate(newDate.getDate() + newNum);
      return newDate;
    case 'h':
      newDate.setHours(newDate.getHours() + newNum);
      return newDate;
    case 'mi':
      newDate.setMinutes(newDate.getMinutes() + newNum);
      return newDate;
    case 's':
      newDate.setSeconds(newDate.getSeconds() + newNum);
      return newDate;
    default:
      newDate.setDate(newDate.getDate() + newNum);
      return newDate;
  }
};
